import React from "react";
// Import the SVG from the same directory as this file
import vaccineFormSvg from "./vaccine form.svg";
// Import shadcn/ui Table components
import {
  Table,
  TableHeader,
  TableRow,
  TableHead,
  TableBody,
  TableCell,
} from "@/components/ui/table";

const VaccineForm = ({ childData }) => {
  return (
    <div className="p-6">
      {/* Header */}
      <h2 className="text-3xl font-bold text-left mb-6">
        Child Immunization Record
      </h2>

      {/* Child Information Table (Plain HTML, no borders) */}
      <table className="w-full mb-6">
        <tbody>
          <tr>
            <td className="px-4 py-2">
              <strong>Child’s Name:</strong> {childData.name}
            </td>
            <td className="px-4 py-2">
              <strong>Mother’s Name:</strong> {childData.motherName}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Date of Birth:</strong> {childData.dob}
            </td>
            <td className="px-4 py-2">
              <strong>Father’s Name:</strong> {childData.fatherName}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Place of Birth:</strong> {childData.placeOfBirth}
            </td>
            <td className="px-4 py-2">
              <strong>Birth Height:</strong> {childData.birthHeight}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Address:</strong> {childData.address}
            </td>
            <td className="px-4 py-2">
              <strong>Birth Weight:</strong> {childData.birthWeight}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Health Center:</strong> {childData.healthCenter}
            </td>
            <td className="px-4 py-2">
              <strong>Sex:</strong> {childData.sex}
            </td>
          </tr>
          <tr>
            <td className="px-4 py-2">
              <strong>Barangay:</strong> {childData.barangay}
            </td>
            <td className="px-4 py-2">
              <strong>Family No.:</strong> {childData.familyNumber}
            </td>
          </tr>
        </tbody>
      </table>

      {/* Immunization Table using shadcn/ui Table Component */}
      {/* The background image and color are applied using an inline style */}
      <Table
        className="w-full border-separate border-spacing-x-2 border-spacing-y-2"
        style={{
          backgroundImage: `url(${vaccineFormSvg})`,
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center",
          backgroundSize: "contain",
        }}
      >
        <TableHeader>
          <TableRow>
            <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
              Bakuna
            </TableHead>
            <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
              Doses
            </TableHead>
            <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
              Petsa ng Bakuna (MM/DD/YY)
            </TableHead>
            <TableHead className="border border-black bg-[#ffb43d] text-black p-2 text-center font-extrabold">
              Remarks
            </TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {childData.vaccinations.map((vaccine, index) => (
            <TableRow key={index} className="bg-white">
              <TableCell className="border border-black p-2 text-center">
                {vaccine.name}
              </TableCell>
              <TableCell className="border border-black p-2 text-center">
                {vaccine.doses}
              </TableCell>
              <TableCell className="p-2 text-center">
                <div className="flex justify-center gap-2">
                  {Array.from({ length: vaccine.doses }).map((_, doseIndex) => (
                    <span
                      key={doseIndex}
                      className="flex-1 border border-black p-1"
                    >
                      {vaccine.date}
                    </span>
                  ))}
                </div>
              </TableCell>
              <TableCell className="border border-black p-2 text-center">
                {vaccine.remarks}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>

      {/* Footer Instructions */}
      <p className="mt-6 text-sm">
        <strong>Instructions:</strong> Sa column ng{" "}
        <strong>Petsa ng Bakuna</strong>, isulat ang petsa ng pagbibigay ng
        bakuna. Sa column ng <strong>Remarks</strong>, isulat ang petsa ng
        pagbalik para sa susunod na dose, o anumang mahalagang impormasyon na
        maaaring makaapekto sa pagbabakuna ng bata.
      </p>
    </div>
  );
};

// Example Data
const exampleData = {
  name: "Juan Dela Cruz",
  dob: "01/01/2023",
  placeOfBirth: "Manila",
  address: "123 Street, Barangay, City",
  motherName: "Maria Dela Cruz",
  fatherName: "Jose Dela Cruz",
  birthHeight: "50cm",
  birthWeight: "3.2kg",
  sex: "Male",
  healthCenter: "Health Center A",
  barangay: "Barangay 1",
  familyNumber: "12345",
  vaccinations: [
    { name: "BCG Vaccine", doses: 1, date: "01/01/2023", remarks: "At birth" },
    {
      name: "Hepatitis B Vaccine",
      doses: 1,
      date: "01/01/2023",
      remarks: "At birth",
    },
    {
      name: "Pentavalent Vaccine",
      doses: 3,
      date: "02/15/2023",
      remarks: "1½ months",
    },
    {
      name: "Oral Polio Vaccine (OPV)",
      doses: 3,
      date: "02/15/2023",
      remarks: "1½ months",
    },
    {
      name: "Inactivated Polio Vaccine (IPV)",
      doses: 2,
      date: "03/20/2023",
      remarks: "3½ months",
    },
    {
      name: "Measles, Mumps, Rubella (MMR)",
      doses: 2,
      date: "10/01/2023",
      remarks: "9 months",
    },
  ],
};

// Page Component
const VaccinePage = () => {
  return <VaccineForm childData={exampleData} />;
};

export default VaccinePage;
